.container {
    max-width: 327px;
    margin: 0 auto;
    // margin-left: 165px;
    // margin-right: 165px;
   
    
    @include for-narrow-layout {
        max-width: $mobile-breakpoint;
    }
    @include for-normal-layout {
        max-width: 573px;
    }
    @include for-wide-layout {
        max-width: 1110px;
    }
}

// GRIDS

.cards-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
    grid-gap: 10px;
}