
.about{
    height: 1000px;
    display: flex;
    // justify-content: space-around;
    flex-direction: column;
    align-items: center;
    background-image: url(../imgs/chairs.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: red;
    background-blend-mode: multiply;

    @include for-normal-layout {
        height: 700px;

    }


    .about-us{
        font-size: 50px;
        font-family: Anton;
        text-transform: uppercase;
        line-height: 60px;
        letter-spacing: 4px;
        margin-top: 50px;
        margin-bottom: 50px;
        color: whitesmoke;
    }


    .about-txt{
        max-width: 30ch;
        margin: 0 30px;
        color: white;
        font-family: Anton;
       
        background-color: #d3d3d330;
        border-radius: 11px;
        padding: 30px;
        
        transition: ease 0.4s;
        font-size: 14px;
        height: 258px;
        @include for-normal-layout {
         
            height: 300px;
            font-size: 16px;
    
        }

        font-size: 14px;
        height: 258px;

        .about-item-title{

            font-size: 20px;
            font-family: Anton;
            text-transform: uppercase;


        }


        &:hover{
            border: 1px solid lightgray;
            background-color: darken($color: #d3d3d330, $amount: 90%);

        }
    }

    .txt-container{
        height: 100%;
        justify-content: space-around;
        display: flex;
        align-items: center;
        flex-direction: column;
       
       
        @include for-normal-layout {

            flex-direction: row;
        }
    


  

    }


}


@keyframes fade-in {
    from {opacity: 0; transform: scale(.7,.7)}
    to {opacity: 1;}
}
.fade-in-element {
  animation: fade-in 1.4s;
}


.hidden {
    opacity: 0;
  }


  


