
.NavBar{
  background-color:  #0000003b;
position: sticky;
opacity: 0.6;
& a{
    color: white;
    font-weight: 600;
    text-decoration: none;
    list-style: none;
    font-size: 20px;
    text-transform: uppercase;
    font-family: Anton;
    margin: 10px 20px;
    position: relative;

    &:hover{
      color: gold;
      
  
  
    }
  
    &:after{
        
        background: none repeat scroll 0 0 transparent;
        bottom: 0;
        content: "";
        display: block;
        height: 2px;
        left: 50%;
        position: absolute;
        background: gold;
        transition: width 0.3s ease 0s, left 0.3s ease 0s;
        width: 0;
  
    }
  
    &:hover:after { 
      width: 100%; 
      left: 0; 
   
    }
   
   
    &:hover{
        cursor: pointer;
        opacity: 0.8;


    }

    &:active{

      color: red;  
    };

    
}

}