.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    animation: show 0.8s;
    z-index: 99999999;
  }
  
  .modal-main {
    position:fixed;
    background: white;
    width: 100%;
 
    height: 100%;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);


    @include for-normal-layout {
      width: 80%;
      height: 450px;
  }

   



  }
  
  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
   
  }

  @keyframes show{
      from{
    transform: scale(0);

      }

        to{

        transform: scale(1);

        }

  }




