* {
    box-sizing: border-box;
    list-style: none;
}

html {
    font-family: montserrat, sans-serif;
    line-height: 1.6;
}

body {
    margin: 0;

}

ul{
    padding: 0;
    margin: 0;
}
img {
    width: 100%;
}

h1, h2, h3, h4, h5, h6 {
    font-family: caveat, serif;
    margin-top: 0;
    font-weight: normal;
}

h1 {
    font-size: rem(60px);
    margin-bottom: rem(20px);
}

p {
    margin: 0 0 rem(12px);
    max-width: 60ch;
}