.card {

 
  // margin: 0 -8px

  .card-text {
    font-size: 18px;
    // max-width: 30ch;
    text-align: center;
    text-decoration: none;
    color: white;
    font-family: Anton;
    margin-top: 10px;
    
  }
  .card-img {
    // width: 350px;
    height: 480px;
    object-fit: fill;
    border-radius: 30px;
    filter: grayscale(80%);
    &:hover{
      cursor: pointer;
      filter: none;
      transform: scale(1.06);
      transition: transform 0.3s;
    }
  }

}

