.main-img {
  height: 90vh;
  width: 100%;
  object-position: center;
  object-fit:unset;
  position: relative;
  // filter: brightness(0.3);
}


.load-more{
  color: white;
  background: none;
  border: none;
  margin-bottom: 30px;
  font-size: 30px;
  text-transform: uppercase;
  cursor: pointer;
  align-self: center;
  position: relative;

  &:hover{
    color: gold;
    


  }

  &:after{
      
      background: none repeat scroll 0 0 transparent;
      bottom: 0;
      content: "";
      display: block;
      height: 2px;
      left: 50%;
      position: absolute;
      background: gold;
      transition: width 0.3s ease 0s, left 0.3s ease 0s;
      width: 0;

  }

  &:hover:after { 
    width: 100%; 
    left: 0; 
 
  }

}

